import type { StoreApi } from 'zustand'

import type { ToastActions, ToastItem } from './types'

type SetState = StoreApi<import('./types').ToastStore>['setState']
type GetState = StoreApi<import('./types').ToastStore>['getState']

/**
 * Create toast actions
 */
export const createToastActions = (set: SetState, get: GetState): ToastActions => ({
  showToast: (toast) => {
    const id = `toast-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`
    const newToast: ToastItem = {
      id,
      variant: 'success',
      duration: 3000,
      position: 'top-right',
      ...toast,
    }

    set((state) => ({
      toasts: [...state.toasts, newToast],
    }))

    // Auto-remove after duration
    if (newToast.duration && newToast.duration > 0) {
      setTimeout(() => {
        get().removeToast(id)
      }, newToast.duration)
    }
  },

  removeToast: (id) => {
    set((state) => ({
      toasts: state.toasts.filter((toast) => toast.id !== id),
    }))
  },
})
