'use client'

import { useCallback, useState } from 'react'

import { Button, Icon, IconComponent } from '@graftcode/design-system'

import styles from './GatewaySelector.module.scss'

interface GatewaySelectorProps {
  gatewayName: string
  loading: boolean
}

export function GatewaySelector({ gatewayName, loading }: GatewaySelectorProps) {
  const [copied, setCopied] = useState(false)

  const truncateName = useCallback((name: string): string => {
    const length = 10
    if (name.length <= length) {
      return name
    }
    return `${name.slice(0, length)}...`
  }, [])

  const handleCopy = useCallback(() => {
    if (loading || !gatewayName) return

    navigator.clipboard.writeText(gatewayName).then(() => {
      setCopied(true)

      // Reset to regular icon after 2 seconds
      setTimeout(() => {
        setCopied(false)
      }, 2000)
    })
  }, [gatewayName, loading])

  if (loading) {
    return null
  }

  const displayText = truncateName(gatewayName)

  return (
    <div className={styles.wrapper}>
      <IconComponent icon={Icon.DSCodeGateway} className={styles.icon} />
      <span className={styles.name}>{displayText}</span>
      <Button
        iconOnly
        icon={copied ? Icon.CopyFilled : Icon.CopyRegular}
        variant="ghost"
        size="s"
        className={styles.copyButton}
        onClick={handleCopy}
        aria-label={`Copy ${gatewayName}`}
        title={`Copy: ${gatewayName}`}
      />
    </div>
  )
}
