'use client'

import { ChangeEvent, useCallback, useEffect, useMemo, useState } from 'react'

import { Icon, IconComponent, SearchBar, Tree, TreeItem } from '@graftcode/design-system'

import { Sidebar } from '@common/core/components/Sidebar'

import { useLibraries } from '@/core/hooks'
import { useUser } from '@/core/hooks/useUser'
import {
  selectExpandedItemsString,
  selectPathString,
  useExpandedItems,
  useNavigationStore,
} from '@/core/stores/navigationStore'
import { filterTreeItems, getExpandedIdsForSearch, mapLibraryToTreeItem } from '@/core/utils/tree'

import styles from './Sidebar.module.scss'

export function VisionSidebar() {
  const { user } = useUser()
  const { gateway, libraries, projectName, isLoading, isError } = useLibraries()

  // Use string selectors for reliable change detection
  const pathString = useNavigationStore(selectPathString)
  const expandedItemsString = useNavigationStore(selectExpandedItemsString)

  // Use custom hooks for arrays (internally use string comparison)
  const expandedItems = useExpandedItems()

  const [searchQuery, setSearchQuery] = useState('')
  const [keyboardShortcut, setKeyboardShortcut] = useState<string | undefined>(undefined)

  // Detect OS and set appropriate keyboard shortcut (not on mobile devices)
  // Use useEffect to avoid hydration mismatch
  useEffect(() => {
    // Check if running on mobile device
    const userAgentData = (navigator as { userAgentData?: { mobile?: boolean; platform?: string } }).userAgentData
    const isMobile =
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
      userAgentData?.mobile === true ||
      window.innerWidth < 768 // Common mobile breakpoint

    if (isMobile) {
      setKeyboardShortcut(undefined)
      return
    }

    // Check if running on Mac
    const isMac = /Mac|iPhone|iPod|iPad/i.test(navigator.platform) || userAgentData?.platform === 'macOS'

    setKeyboardShortcut(isMac ? '⌘N' : 'Ctrl+F')
  }, [])

  // Build base tree items from libraries
  const baseTreeItems = useMemo((): TreeItem[] => {
    // Don't build tree if data is still loading or not available
    if (isLoading || !projectName || Object.keys(libraries).length === 0) {
      return []
    }

    return [
      {
        id: 'root',
        label: projectName || gateway,
        icon: Icon.DSCodeGateway,
        type: 'root',
        children: Object.values(libraries).map(mapLibraryToTreeItem),
      },
    ]
  }, [projectName, gateway, libraries, isLoading])

  // Filter tree items based on search query
  const treeItems = useMemo((): TreeItem[] => {
    // If searching, filter the tree and show results even if nothing is expanded
    if (searchQuery.trim()) {
      return filterTreeItems(baseTreeItems, searchQuery)
    }

    // Otherwise, only show tree when root is expanded (original behavior)
    if (expandedItems.length === 0) {
      return []
    }

    return baseTreeItems
  }, [baseTreeItems, expandedItems.length, searchQuery])

  // Auto-expand root item when rendering first time (when data is available)
  useEffect(() => {
    if (baseTreeItems.length > 0 && !searchQuery.trim()) {
      const store = useNavigationStore.getState()

      // Always ensure root is expanded when we have tree items
      if (!store.expandedItems.includes('root')) {
        store.setExpandedItems([...store.expandedItems, 'root'])
      }

      // If no path is set, navigate to first library
      if (store.path.length === 0) {
        const root = baseTreeItems[0]
        const firstLibrary = root?.children?.[0]

        if (firstLibrary) {
          store.navigateTo([firstLibrary.id])
        }
      }
    }
  }, [baseTreeItems, searchQuery])

  // Auto-expand matching items when searching
  useEffect(() => {
    if (searchQuery.trim() && baseTreeItems.length > 0) {
      const searchExpandedIds = getExpandedIdsForSearch(baseTreeItems, searchQuery)
      const store = useNavigationStore.getState()
      // Only update if the expanded items are different to avoid unnecessary updates
      const currentExpanded = store.expandedItems
      const needsUpdate =
        searchExpandedIds.length !== currentExpanded.length ||
        !searchExpandedIds.every((id) => currentExpanded.includes(id))

      if (needsUpdate) {
        store.setExpandedItems(searchExpandedIds)
      }
    }
  }, [searchQuery, baseTreeItems])

  // Handle tree item click - navigate to path
  // Get fresh action at call time to avoid stale closures
  const handleItemClick = useCallback((item: TreeItem) => {
    const newPath = item.id === 'root' ? [] : item.id.split('/')
    useNavigationStore.getState().navigateTo(newPath)
  }, [])

  // Handle tree item toggle (expand/collapse)
  // Get fresh action at call time to avoid stale closures
  const handleItemToggle = useCallback((item: TreeItem, expanded: boolean) => {
    // Prevent root from being collapsed
    if (item.id === 'root' && !expanded) {
      return
    }

    const store = useNavigationStore.getState()

    if (expanded) {
      // When expanding, navigate to the item (navigateTo automatically expands the path)
      const newPath = item.id === 'root' ? [] : item.id.split('/')
      store.navigateTo(newPath)
    } else {
      // When collapsing, collapse the item and navigate to PARENT route
      store.toggleExpanded(item.id)

      // Get parent path by removing the last segment
      const itemPath = item.id.split('/')
      const parentPath = itemPath.slice(0, -1) // Remove last segment to get parent
      store.navigateTo(parentPath)
    }
  }, [])

  // Handle search input change
  const handleSearchChange = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    setSearchQuery(event.target.value)
  }, [])

  // Handle search clear
  const handleSearchClear = useCallback(() => {
    setSearchQuery('')
  }, [])

  // Handle ⌘N (Mac) or Ctrl+F (Windows/Linux) keyboard shortcut to focus search
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      // Check for ⌘N on Mac or Ctrl+F on Windows/Linux
      const isMacShortcut = event.metaKey && !event.ctrlKey && event.key === 'n'
      const isWindowsShortcut = event.ctrlKey && !event.metaKey && event.key === 'f'

      if (isMacShortcut || isWindowsShortcut) {
        // Prevent default browser behavior (new window for ⌘N, find dialog for Ctrl+F)
        event.preventDefault()
        event.stopPropagation()
        event.stopImmediatePropagation()

        // Small delay to ensure SearchBar is rendered
        requestAnimationFrame(() => {
          // Try multiple selectors to find the search input within sidebar
          const sidebar = document.querySelector('aside.sidebar')
          if (!sidebar) return

          const selectors = [
            `input[type="search"]`,
            `input[type="text"]`,
            `input[placeholder*="Search" i]`,
            `.${styles.content} input`,
            'aside.sidebar input',
          ]

          let searchInput: HTMLInputElement | null = null
          for (const selector of selectors) {
            // Search within sidebar first
            searchInput = sidebar.querySelector(selector) as HTMLInputElement
            if (searchInput && searchInput.offsetParent !== null) {
              // Check if element is visible
              break
            }
            // Fallback to document-wide search
            if (!searchInput) {
              searchInput = document.querySelector(selector) as HTMLInputElement
              if (searchInput && searchInput.offsetParent !== null) break
            }
            searchInput = null
          }

          if (searchInput) {
            searchInput.focus()
            searchInput.select()
          }
        })
      }
    }

    // Use capture phase to catch event before other handlers
    window.addEventListener('keydown', handleKeyDown, true)
    return () => {
      window.removeEventListener('keydown', handleKeyDown, true)
    }
  }, [])

  // Create a unique key that changes when either path or expanded items change
  // This forces Tree to re-render and reflect current navigation state
  const treeKey = `${pathString}::${expandedItemsString}::${searchQuery}`

  // Render content based on state - error takes priority
  const renderContent = () => {
    if (isError && !isLoading) {
      return null
    }

    return (
      <div className={styles.content}>
        <SearchBar
          className={styles.search}
          value={searchQuery}
          onChange={handleSearchChange}
          onClear={handleSearchClear}
          size="md"
          shortcut={keyboardShortcut}
          version="gray"
        />
        {treeItems.length > 0 ? (
          <Tree
            key={treeKey}
            className={styles.tree}
            items={treeItems}
            expandedItemIds={expandedItems}
            activeItemId={pathString || undefined}
            showBadges
            onItemClick={handleItemClick}
            onItemToggle={handleItemToggle}
          />
        ) : searchQuery.trim() ? (
          <div className={styles.noResults}>
            <IconComponent icon={Icon.SearchRegular} className={styles.noResultsIcon} />
            <span className={styles.noResultsTitle}>No results found</span>
            <span className={styles.noResultsMessage}>Try a different search term</span>
          </div>
        ) : null}
      </div>
    )
  }

  return (
    <Sidebar selectedNavId="vision" isCollapsable={false} showWorkspaceSelector={!!user} navbarHidden={isError}>
      {renderContent()}
    </Sidebar>
  )
}
