'use client'

import type { PropsWithChildren } from 'react'
import { useCallback, useMemo, useState } from 'react'

import { Button, Icon, TreeItem } from '@graftcode/design-system'
import type { NavGroup } from '@graftcode/design-system/dist/core/components/Navbar/Navbar.types'

import { ErrorBoundary, Header, Loader, MainContentHeader, UnauthenticatedBanner } from '@common/core/components'
import { DEFAULT_PROFILE_ITEMS } from '@common/core/components'
import { VisionMobileMenu } from '@common/core/components/MobileMenu/variants/VisionMobileMenu'
import { getNavigationUrls } from '@common/core/config/navigation'
import { useLayoutContext } from '@common/core/contexts'

import { ErrorState } from '@/core/components/ErrorState'
import { GatewaySelector } from '@/core/components/GatewaySelector'
import { VisionSidebar } from '@/core/components/Sidebar/Sidebar'
import { useLibraries } from '@/core/hooks'
import { selectPathString, useExpandedItems, useNavigationStore } from '@/core/stores/navigationStore'
import { mapLibraryToTreeItem } from '@/core/utils/tree'

import '@styles/layout/layout.scss'
import styles from './Layout.module.scss'

const VISION_MENU_GROUPS: NavGroup[] = []

export function LayoutClient({ children }: PropsWithChildren) {
  const { isVisionLocal } = useLayoutContext()
  const { gateway, libraries, gatewayPath, isLoading, isError, refetch, projectName } = useLibraries()
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)

  // Navigation store for tree state
  const pathString = useNavigationStore(selectPathString)
  const expandedItems = useExpandedItems()

  // Build tree items from libraries
  const treeItems = useMemo((): TreeItem[] => {
    if (isLoading || !projectName || Object.keys(libraries).length === 0) {
      return []
    }

    return [
      {
        id: 'root',
        label: projectName || gateway,
        icon: Icon.DSCodeGateway,
        type: 'root',
        children: Object.values(libraries).map(mapLibraryToTreeItem),
      },
    ]
  }, [projectName, gateway, libraries, isLoading])

  const handleSignUpClick = useCallback(() => {
    // When Vision is local, use production portal URL
    const urls = getNavigationUrls()
    window.location.href = urls.portal
  }, [])

  const handleRetry = useCallback(() => {
    refetch()
  }, [refetch])

  const handleRefresh = useCallback(() => {
    refetch()
  }, [refetch])

  const handleCreateGateway = useCallback(() => {
    // TODO: Implement create gateway functionality
  }, [])

  const handleMenuToggle = useCallback(() => {
    setMobileMenuOpen((prev) => !prev)
  }, [])

  const handleMobileMenuClose = useCallback(() => {
    setMobileMenuOpen(false)
  }, [])

  const handleProfileClick = useCallback((item: string) => {
    if (item === 'logout') {
      window.location.href = '/logout'
    }
  }, [])

  const handleNavigationClick = useCallback((url: string) => {
    window.location.href = `/${url}`
  }, [])

  // Tree navigation handlers
  const handleTreeItemClick = useCallback((item: TreeItem) => {
    const newPath = item.id === 'root' ? [] : item.id.split('/')

    // Close mobile menu when selecting a leaf item (no children)
    const isLeaf = !item.children || item.children.length === 0
    if (isLeaf) {
      // Close menu first, then navigate after menu close animation completes
      // Menu animation is 300ms, body overflow is restored after that
      // Need to wait for this before scroll position calculations work correctly
      setMobileMenuOpen(false)
      setTimeout(() => {
        useNavigationStore.getState().navigateTo(newPath)
      }, 350)
    } else {
      useNavigationStore.getState().navigateTo(newPath)
    }
  }, [])

  const handleTreeItemToggle = useCallback((item: TreeItem, expanded: boolean) => {
    if (item.id === 'root' && !expanded) {
      return
    }

    const store = useNavigationStore.getState()

    if (expanded) {
      const newPath = item.id === 'root' ? [] : item.id.split('/')
      store.navigateTo(newPath)
    } else {
      store.toggleExpanded(item.id)
      const itemPath = item.id.split('/')
      const parentPath = itemPath.slice(0, -1)
      store.navigateTo(parentPath)
    }
  }, [])

  return (
    <div className={`app-layout ${isError ? 'is-error' : ''}`}>
      <Loader />
      <aside className="sidebar">
        <VisionSidebar />
      </aside>

      <div className="main-content">
        <header className={`topbar ${styles.unauthenticated}`}>
          {isError ? (
            <Header
              profileItems={DEFAULT_PROFILE_ITEMS}
              searchDisabled
              notificationsDisabled
              onProfileClick={handleProfileClick}
              onMenuToggle={handleMenuToggle}
            />
          ) : (
            <>
              {!isLoading && <GatewaySelector gatewayName={gatewayPath || projectName || ''} loading={isLoading} />}
              <div className={styles.mobileMenuToggle}>
                <Button iconOnly size="l" variant="ghost" icon={Icon.NavigationRegular} onClick={handleMenuToggle} />
              </div>
            </>
          )}
        </header>

        <main className="content">
          {isError ? (
            <>
              <ErrorState onRetry={handleRetry} />
              {isVisionLocal && <UnauthenticatedBanner onSignUpClick={handleSignUpClick} />}
            </>
          ) : (
            <>
              <div className={styles.contentHeader}>
                <MainContentHeader
                  title="Vision"
                  description="See library installation command and see all methods and try them out below"
                  actionButtonLabel="Create gateway"
                  actionButtonIcon={Icon.AddRegular}
                  showActionButton={!isVisionLocal}
                  onRefreshClick={handleRefresh}
                  onActionClick={handleCreateGateway}
                />
              </div>
              <ErrorBoundary>{children}</ErrorBoundary>
              <UnauthenticatedBanner onSignUpClick={handleSignUpClick} />
            </>
          )}
        </main>
      </div>

      <VisionMobileMenu
        isOpen={mobileMenuOpen}
        onClose={handleMobileMenuClose}
        menuGroups={VISION_MENU_GROUPS}
        onProfileClick={handleProfileClick}
        onNavigationClick={handleNavigationClick}
        treeItems={treeItems}
        expandedItemIds={expandedItems}
        activeItemId={pathString || undefined}
        onTreeItemClick={handleTreeItemClick}
        onTreeItemToggle={handleTreeItemToggle}
        isLoading={isLoading}
        isError={isError}
      />
    </div>
  )
}
